.hover-effect {
    position: relative;
  display: inline-block;
  padding: 5px 10px;
  font-size: 20px;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: #000;
  transition: all 0.4s ease;
  }
  
  .hover-effect::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 3px;
    background-color:  rgb(74, 7, 100);;
    transform: scaleX(0);
    transform-origin: bottom right;
    transition: transform 0.4s ease;
  }

  .hover-effect:hover {
    transition: 0.7s;
    color: #fff;
  }


  .hover-effect:hover::before{
    transform: scaleX(1);
    transform-origin: bottom left;
  }
  
 
  